                  <div class="chr-box">
                    <div v-for="(person,index) of item.meet.meeter" :key="index">
                      <!-- <img :src="person.url" alt=""> -->
                       <img :src="person.url==''?'../../../static/images/header_userimg.png':person.url" alt="">
                      <span>{{person.name}}</span>
                    </div>
                  </div>
                  
                </div>
              </div>
            </div>
            <!-- <div class="approverItem-comments-total" v-if="!item.showComment">还没有评论</div>
            <div class="approverItem-comments-total" v-if="item.showComment&&item.comment.length!=0">{{item.comment.length}}条评论</div>
            <div class="approverItem-commentslist" v-if="item.showComment&&item.showall">          
