            </div>
          </div>
        </div>
      </div>
      <div class="approverItem-loadingbox" v-if="discuss.discusslist.length>3" @click="changediscussShowall()">
        <em v-if="discuss.showall">加载更多</em>
        <em v-if="!discuss.showall">收起</em>
      </div>
      <div class="approverItem-commentbox">
        <input class="approverItem-commentbox-input" v-model="discuss.content" placeholder="写下你的总评">
