  </div>
</template>
<script>
import { dialogConfirm, compareJson, dialogConfirmHasbtn } from '../../config/mUtils'
export default {
  data() {
    return {}
  },
  mounted() {
  },
  created() {
  },
  computed: {},
  props: ['approve','discuss','type'],
  methods: {
    getImgurl(e) {
      e.currentTarget.src = '../../../static/images/header_userimg.png';
      e.onerror = null;
    },
    createCommentFun(hid,cid,content,cindex){
      if(content==''){
        dialogConfirm('info','评论不能为空')
      }else{
        let param={
          param:{
            hid:hid,
            comment_id:cid,
            content:content
          },
          cindex:cindex
        }
        this.$emit('createCommentFun',param);
      } 
    },
    //回复
    replyclick(cid,cindex,rname){
      let param={
          cindex:cindex,
          comment_id:cid,
          replier:rname,
          cindex:cindex
        }
      this.$emit('createreplyCommentFun',param)
    },
    //取消回复
    cancelReply(cindex){
      let param={
          cindex:cindex
        }
      this.$emit('cancelReplyFun',param)
    },
    changeShowall(cindex){
      let param={
          cindex:cindex
        }
      this.$emit('changeShowallFun',param)
    },
    changediscussShowall(){
      this.$emit('changediscussShowall')
    },
    //总评
    createDiscuss(e){
      if(e==''){
        this.dialogConfirm('info','评论不能为空')
      }else{
        this.$emit('createDiscuss',e)
      }  
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/mixin';
@import '../../style/apply';
.createapply-formgroup-content {
  margin-left: 0px;
}
.blue_title{
  background-color: #3C98E8;
  color: white !important;
  border: none !important;
}
.viewapply-content-item-imglist{
  .viewapply-content-item-imgItem{
    margin-bottom: 10px;
    .ch_title{
      font-size: 14px;
    }
    .chr-box{
      position: relative;
      &>div{
        margin-right: 26px;
        min-width: 80px
      }
      img{
        width: 22px;
        height: 22px;
        position: relative;
        top:2px;
        margin-right: 10px;
        border-radius: 50%
      }
      span{
        position: relative;
        top:-3px;
      }
    }
  }
  .blue_time{
    color:#3C98E8;
    font-size: 14px;
  }
}
.approverItem-time{
  padding-top: 4px;
}
</style>



