							idCard: item.idCard,
							name: item.name,
							phone: '',
							type: 0,
						};
						ae.push(newObj);
					}
				});
				if(ae.length == 0) {
					dialogConfirm('info', "请先添加新的联系人");
					return false;
				}
				params.ae = ae;
				surveyCredit(params).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '查询成功');
						this.getComBaseInfo();
