				if(id != undefined) {
					this.$router.push({
						path: '/home/creditQueryView',
						query: {
							id: id,
							companyId: this.companyId,
							cName: this.cName
						}
					})
				} else {
					let selectedPeople = [];
					this.baseInfo.relate.forEach(item => {
						if(item.credit_id != null && item.status == 2) {
							selectedPeople.push(item.credit_id);
						}
					});
					if(selectedPeople.length == 1) {
						this.$router.push({
							path: '/home/creditQueryView',
							query: {
								id: selectedPeople[0],
								companyId: this.companyId,
								cName: this.cName
							}
						})
					} else if(selectedPeople.length > 1) {
						this.$router.push({
							path: '/home/creditQueryView',
							query: {
								id: selectedPeople.join(','),
								companyId: this.companyId,
								cName: this.cName
