			//-----------------------------关联基金方法-----------------------------------------
			getFundList() {
				allProjectOrFund({
					type: 2,
					flag: 0,
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.fundList = res.data.payload.data;
