			getInvestSubject() {
				investSubject({
					company_id: this.companyId
				}).then(res => {
					if(res.data.msgNo == 1) {
						// console.log(res.data.payload);
						let data = res.data.payload;
						if(data == null || data.length == 0) {
							this.fundArr = [];
						} else {
							this.fundArr = data;
							this.fundArr.forEach(item => {
								item.had_invest = item.had_invest / 10000
							});
						}
					} else {
						dialogConfirm('info', res.data.message)
					}
				})
			},
			toAddFund() {
				let newObj = {
					fundName: '',
					fund_id: '',
					had_invest: '',
					proportion: '',
				};
				this.fundArr.push(newObj);
			},
			toDeleteFund(index) {
				this.fundArr.splice(index, 1);
			},
			toCommitFund() {
				let newFundArr = JSON.parse(JSON.stringify(this.fundArr));
				let canCommit = true;
				if(newFundArr.length == 0) {
					dialogConfirm('info', '请添加关联基金数据');
					return false;
				}
				newFundArr.forEach(item => {
					delete item.fundName;
					if(item.had_invest == '') {
						item.had_invest = 0;
					} else {
						item.had_invest = parseFloat(item.had_invest) * 10000;
					}
					if(item.proportion == '') {
						item.proportion = 0;
					} else {
						item.proportion = parseFloat(item.proportion);
					}
					if(item.fund_id == '') {
						canCommit = false;
					}
				});
				if(!canCommit) {
					dialogConfirm('info', '未选择投资主体!');
					return false;
				}

				newFundArr = JSON.stringify(newFundArr)
				submitInvest({
					company_id: this.companyId,
					data: newFundArr
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '提交成功');
						this.getInvestSubject();
					} else {
						dialogConfirm('info', res.data.message)
					}
				})

			},
			selectFund(index, fundId) {
				this.fundArr.forEach((item, index1) => {
					if(index1 != index && item.fund_id == fundId) {
						this.fundArr.forEach((item2, index2) => {
							if(index2 == index) {
								item2.fund_id = ''
							}
						});
						dialogConfirm('info', '该基金已存在!')
					}
				});
			},

