				if(type == 4) {
					if(typeof id == 'string') {
						this.baseInfo.relate[index].position = id;
					} else {
						list.forEach(element => {
							if(element.position_id == id) {
								this.baseInfo.relate[index].position = element.position
							}
						});
					}
				} else {
					list.forEach(element => {
						if(type == 1) {
							if(element.id == id) {
								this.baseInfo.duty.name = element.name;
							}
						} else if(type == 2) {
							if(element.id == id) {
								this.baseInfo.lead.name = element.name;
							}
						} else if(type == 3) {
							if(element.id == id) {
								this.baseInfo.relate[index].name = element.name
							}
							if(typeof id == 'string') {
								this.baseInfo.relate[index].name = id;
							}
						}
					});
				}
			},
