			//保存提交 基本信息 
			toEditorBaseInfo(type) {
				let relate = JSON.parse(JSON.stringify(this.baseInfo.relate));
				relate.forEach(item => {
					if(typeof item.position_id == 'string') {
						item.position_id = 0;
					}
					if(typeof item.id == 'string') {
						delete item.id
					}
					delete item.sum;
					delete item.credit_id
					delete item.update_time
					delete item.status

				});

				let params = {
					cName: this.cName,
					type: type,
					relate: relate,
					principal: this.baseInfo.duty.principal,
					leader: this.baseInfo.lead.leader,
					shortName: this.baseInfo.shortName,
					info: this.baseInfo.info,
					stage: this.companyStatus
				}
				if(!this.isEditor) {
					this.options4.forEach(element => {
						if(element.name == this.cName) {
							if(element.id == undefined) {
								params.tianyan_id = '';
							} else {
								params.tianyan_id = element.id;
							}
						}
					})
				}
				if(this.companyId != '') {
					params.company_id = this.companyId;
				}
				let status = true
				//必填项
				if(params.cName == '' || params.principal == '' || params.leader == '' || params.cName == undefined || params.principal == undefined || params.leader == undefined) {
					dialogConfirm('info', '存在必填项未填写!!')
					return false
				}
				params.relate.forEach(element => {
					if(element.name == '' || element.phone == '' || element.position == '' || element.idCard == '') {
						// if (element.name == '' || element.phone == '' || element.position == '') {
						status = false;
						dialogConfirm('info', '存在必填项未填写!!')
						return false

					}
				})

				//格式
				if(this.showXydm) {
					let xydmTest = /[0-9A-HJ-NPQRTUWXY]{2}\d{6}[0-9A-HJ-NPQRTUWXY]{10}/;
					if(this.xydm == '') {
						params.creditCode = ''
					} else {
						if(!xydmTest.test(this.xydm)) {
							dialogConfirm('info', '统一社会信用代码格式错误')
							return false;
						} else {
							params.creditCode = this.xydm;
						}
					}
				}

				if(status) {
					params.relate.forEach(element => {
						let phoneTest = /^[1][3,4,5,7,8][0-9]{9}$/;
						let emailTest = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
						let idCardTest = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
						if(!phoneTest.test(element.phone)) {
							dialogConfirm('info', '手机号格式存在错误')
							status = false;
							return false;
						}
						if(element.email != '') {
							if(!emailTest.test(element.email)) {
								dialogConfirm('info', '邮箱格式存在错误')
								status = false
								return false;
							}
						}
						if(!idCardTest.test(element.idCard)) {
							dialogConfirm('info', '身份证格式存在错误')
							status = false;
							return false;
						}
					});
				}
				if(!status) {
					return false;
				}

				if(params.relate.length == 0) {
					delete params.relate;
				}
				newProject(params).then(res => {
					if(res.data.msgNo == 1) {
						if(type == 1) {
							dialogConfirm('success', '保存成功')
							this.toGetPosition();
							this.getComBaseInfo();
							this.base_editor = true;
						} else {
							if(!this.isEditor) {
								dialogConfirm('success', '创建成功')
								if(this.showXydm) {
									this.$router.push({
										name: 'investProjectManageList'
									});
								} else {
									this.$router.push({
										name: 'newProjectProcessList'
									});
								}

							} else {
								dialogConfirm('success', '提交成功')
								this.toGetPosition();
								this.getComBaseInfo();
								this.base_editor = true;
							}
						}
					} else if(res.data.msgNo == 9527) {
						this.confirmData = res.data.payload[0];
						this.addaccountDialogVisible1 = true;
					} else {
						dialogConfirm('info', res.data.message)
					}
				})
			},

			//-------------------------------------相关材料部分的方法-------------------------------------
			//获取立项材料  
			getLxInfo(type) {
				let params = {
					company_id: this.companyId
				};
				let canContinue = true;
