			},
			//上传文件
			uploadfile($event, item, type) {
				let files = $event.target.files;
				// console.log(item);
				// console.log(files.length);
				if(type == 'lxbg') {
					for(let i of files) {;
						let newFileObj = {
							file_name: i.name,
							file: i
						}
						item.files.push(newFileObj);
					}
				} else {
					for(let i of files) {;
						let newFileObj = {
							file_name: i.name,
							file: i
						}
						// console.log(item)
						item.push(newFileObj);

					}
				}
				$event.target.value = '';
			},
			uploadfileRequest($event) {
				let item = $event.data.item;
				let type = $event.data.type;
				let files = [];
				let filetype = getFileTypeMutil($event.file.name);
				if(filetype == "") {
					dialogConfirm('info', '不支持该类型文件上传');
					return;
				} else {
					files.push($event.file);
				}
				if(type == 'lxbg') {
					for(let i of files) {;
						let newFileObj = {
							file_name: i.name,
							file: i
						}
						item.files.push(newFileObj);
					}
				} else {
					for(let i of files) {;
						let newFileObj = {
							file_name: i.name,
							file: i
						}
						// console.log(item)
						item.push(newFileObj);

					}
				}
			},
			//替换文件
			changefile(items, index, $event) {
				let files = $event.target.files;
				if(files.length == 0) {
					return false;
				}
				if(items[index].file_id == undefined) {
					let newFileObj = {
						file_name: files[0].name,
						file: files[0]
					}
					items.splice(index, 1, newFileObj)
				} else {
					this.deleteFile(items, index, files);
					let newFileObj = {
						file_name: files[0].name,
						file: files[0]
					}
				}
			},
			//删除文件
			deleteFile(items, index, files) {
				if(items[index].file_id == undefined) {
					items.splice(index, 1);
				} else {
					companyDeleteFile({
						file_id: items[index].file_id
					}).then(res => {
						if(res.data.msgNo == 1) {
							items.splice(index, 1);
							if(files != undefined) {
								let newFileObj = {
									file_name: files[0].name,
									file: files[0]
								}
								if(items.length == 0) {
									items.push(newFileObj);
								} else {
									items.splice(index, 0, newFileObj)
								}
							}
						} else {
							dialogConfirm('info', res.data.message);
						}
					})
				}

			},
			//提交立项材料和table信息 
			toCommit(type, floor, type1) {
				let conmmitData = [];
				let canCommite = true;
				let params = {
					company_id: parseInt(this.companyId),
					type: type1,
					floor: this.step
				};
				if(parseInt(floor) + 1 == this.step) {
					params.current = 1
				} else {
					params.current = 0
				}
				let haveFund = false;
				if(parseInt(floor) + 1 == this.invest) {
					if(this.fundArr.length == 0) {
						dialogConfirm('info', '请添加关联基金数据');
						return false;
					} else {
						this.fundArr.forEach(element => {
							if(element.id !== undefined) {
								haveFund = true;
							}
						})
						if(!haveFund) {
							dialogConfirm('info', '请添加关联基金数据');
							return false;
						}
					}
				}

				if(type == 'lxbg') {
					conmmitData = this.lxInfo;
				} else if(type == 'ysh') {
					conmmitData = this.ysh_File;
				} else {
					let newArr = [];
					type.materialList.forEach(element => {
						newArr.push(element);
					});
					type.tableList.forEach(element => {
						newArr.push(element);
					});
					conmmitData = newArr
				}
				conmmitData.forEach(element => {
					if(element.control == 1) {
						let newObj = {
							class_id: element.class_id,
							frame: element.frame
						}
						params.table = JSON.stringify(newObj);
					} else {
						if(element.son == undefined && element.files.length > 0) {
							params[element.class_id] = [];
							element.files.forEach(item => {
								if(item.file_id == undefined) {
									params[element.class_id].push(item.file);
								}
							});
							if(params[element.class_id].length == 0) {
								delete params[element.class_id];
							}
						} else if(element.son != undefined) {
							element.son.forEach(item11 => {
								params[item11.class_id] = [];
								item11.files.forEach(item12 => {
									if(item12.file_id == undefined) {
										params[item11.class_id].push(item12.file);
									}
								});
								if(params[item11.class_id].length == 0) {
									delete params[item11.class_id];
								}
							});
						}
					}
				});
				uploadMoreFile(params).then(res => {
					if(res.data.msgNo == 1) {
						if(type1 == 1) {
							dialogConfirm('success', '保存成功');
						} else if(type1 == 2) {
							dialogConfirm('success', '提交成功');
						}
						this.getComInfo();
						this.initData1();
						this.toEditorModule(floor);
					} else {
						dialogConfirm('info', res.data.message)
					}
				})
			},

			//-------------------------------------------审批部份---------------------------------------------
			//获取审批人信息
			toGetSpInfo() {
				getSpInfo({
					floor: this.step,
				}).then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload.sp;
						let newArr = [];
						data.forEach((item, index2) => {
							item.forEach((item1, index) => {
								if(index == item.length - 1) {
									newArr.push(item1);
									if(index2 != data.length - 1) {
										newArr.push({
											isstep: true,
										});
									}
								} else {
									newArr.push(item1);
								}
							});
						});
						this.spPersonInfo = newArr;
					}
				})
			},
			//获取审批记录 
			initData1() {
				approveRecord({
					company_id: this.companyId,
				}).then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload.flow;
						let button = res.data.payload.button
						this.click = res.data.payload.click;
						this.buttonArr = [];
						if(JSON.stringify(button) != "{}") {
							this.showSpyj = true;
							for(let i in button) {
								let buttonObj = {}
								buttonObj.status = parseInt(i);
								buttonObj.name = button[i];
								this.buttonArr.push(buttonObj);
							}
							this.buttonArr.sort(this.compareValue('status'))
						}
						// console.log(this.buttonArr);
						if(JSON.stringify(data) != "{}") {
							// let count = 0;
							// let number = 0;
							// for (let i in data) {
							//   count++;
							// }

							for(let i in data) {
								// number++;
								data[i].forEach(item1 => {
									item1.forEach(item => {
										item.showOpinion = false;
										item.showComment = true;
										item.attach = item.file;
										if(item.attach.length != 0) {
											item.showOpinion = true;
										} else {
											if(item.content != '' && item.content != null) {
												item.showOpinion = true;
											} else {
												if(JSON.stringify(item.meet) != '{}') {
													item.showOpinion = true;
												}
											}
										}
										if(item.attach == undefined) {
											item.attach = [];
										} else {
											this.getFileType(item.attach)
										}
									})

								})
								// if (i == '1') {
								//   this.approve_lx = data[i];
								// } else if (i == '2') {
								//   this.approve_ysh = data[i]
								// }
								// if (number == count) {
								//   data[i].forEach((item, index) => {
								//     if (index == data[i].length - 1) {
								//       this.conmmitSpId = item.approval_id;
								//     }
								//   });
								// }
							}
							this.approveRecordList = data;
						}
					}
				})
			},
			//提交审批信息（不包括开会信息）
			tocommitSp(type, floor) {
				if(type == 2) {
					this.toOpenRuleDialog(type, floor);
					return false;
				}
				if(type == 4) {
					this.transferManager(this.step);
					return false;
				}
				let data = {
					company_id: this.companyId,
					type: type,
					floor: this.step,
					content: this.conmmitSpObj.content
				}
				if(parseInt(floor) + 1 == this.step) {
					data.current = 1
				} else {
					data.current = 0
				}

				let files = [];
				this.conmmitSpObj.file.forEach(element => {
					files.push(element.file);
				});

				companySubmitApprove(data, files).then(res => {
					if(res.data.msgNo == 1) {
						this.initData1();
						this.conmmitSpObj = {
							file: [],
							content: ''
						}
						this.getComInfo();
						this.initData1();
						dialogConfirm('success', '提交成功')
					} else {
						dialogConfirm('info', res.data.message);
					}
				})
			},
			//提交审批信息（包括开会信息）
			commitRule() {
				let data = {
					company_id: this.companyId,
					type: this.conmmitSpObjAddTime.type,
					floor: this.conmmitSpObjAddTime.floor,
					content: this.conmmitSpObj.content,
					meeting_time: this.conmmitSpObjAddTime.time,
				}
				if(parseInt(this.conmmitSpObjAddTime.floor) + 1 == this.step) {
					data.current = 1
				} else {
					data.current = 0
				}
				let files = [];
				this.conmmitSpObj.file.forEach(element => {
					files.push(element.file);
				});
				let idArr = []
				this.conmmitSpObjAddTime.userList.forEach(item => {
					idArr.push(item.id);
				});
				if(idArr.length > 0) {
					idArr = idArr.join(',')
					data.copier = idArr
				} else {
					idArr = '';
					data.copier = idArr
				}
				if(data.meeting_time == '' || data.meeting_time == null) {
					dialogConfirm('info', '请选择会议时间')
					return false;
				} else {
					data.meeting_time = data.meeting_time / 1000;
				}
				if(data.copier == '') {
					dialogConfirm('info', '请选择参会人员')
					return false;
				}
				companySubmitApprove(data, files).then(res => {
					if(res.data.msgNo == 1) {
						this.initData1();
						this.conmmitSpObj = {
							file: [],
							content: ''
						}
						this.getComInfo();
						this.initData1();
						dialogConfirm('success', '提交成功')
						this.ruleDialog = false;
					} else {
						dialogConfirm('info', res.data.message);
					}
				})
			},
			// 打开参会人员弹窗时触发
			toOpenRuleDialog(type, floor) {
				this.ruleDialog = true;
				this.conmmitSpObjAddTime = {
					time: '',
					userList: [],
					type: type,
					floor: floor
				}
				this.getMeetInfo(floor);
			},
			//关闭弹窗之前调用
			ruleClose() {
				this.ruleDialog = false;
			},
			//获取默认参会会人员
			getMeetInfo(status1) {
				meetInfo({
					floor: status1
				}).then(res => {
					if(res.data.msgNo == 1) {
						res.data.payload.meeter.forEach(element => {
							element.id = element.uid;
						});
						this.conmmitSpObjAddTime.userList = res.data.payload.meeter
					}
				})
			},
			//选择参会人员
			changeValue1() {
				if(this.conmmitSpObjArrValue == 0) {
					this.conmmitSpObjArr.forEach(element => {
						if(element.id != 0) {
							let newObj = {}
							newObj.id = element.id;
							newObj.name = element.name
							newObj.url = element.url
							let newStatus = true;
							this.conmmitSpObjAddTime.userList.forEach(item => {
								if(item.id == element.id) {
									newStatus = false
								}
							});
							if(newStatus) {
								this.conmmitSpObjAddTime.userList.push(newObj);
							}
							this.conmmitSpObjArrValue = '';
						}
					});
				} else {
					let newObj = {}
					this.conmmitSpObjArr.forEach(element => {
						if(element.id == this.conmmitSpObjArrValue) {
							newObj.id = element.id;
							newObj.name = element.name
							newObj.url = element.url
							let newStatus = true;
							this.conmmitSpObjAddTime.userList.forEach(item => {
								if(item.id == element.id) {
									newStatus = false
								}
							});
							if(newStatus) {
								this.conmmitSpObjAddTime.userList.push(newObj);
							} else {
								dialogConfirm('info', '该人员已添加')
							}
							this.conmmitSpObjArrValue = '';
						}
					});
				}

			},
			//删除选中的开会的人员
			deleteSelectedItem(index) {
				this.conmmitSpObjAddTime.userList.splice(index, 1)
			},

			transferManager(floor) {
				this.allotApprovalObj = {
					company_id: this.companyId,
					floor: floor,
				}
				this.transferAdminVisibleDialog = true;
			},
			changeModalShow(type, name) {
				this[name + 'VisibleDialog'] = type
			},
			getModaldata1(list, liststr) {
				// console.log(list);
				// console.log(liststr);
				this.allotApprovalObj.user_id = liststr.id;
				allotApproval(this.allotApprovalObj).then(res => {
					if(res.data.msgNo == 1) {
						this.getComInfo();
						this.initData1();
						this.toGetSpInfo();
						dialogConfirm('success', '分配成功')
					} else {
						dialogConfirm('info', res.data.message);
					}
				})
			},
			remoteMethod(query) {
				if(query !== '') {
					this.loading = true;
					getcomPany({
						name: query
					}).then(res => {
						this.loading = false;
						if(res.data.msgNo == 1) {
							if(res.data.payload.length == 0) {
								this.options4 = [{
									name: query
								}]
							} else {
								let newObj = {
									name: query
								}
								res.data.payload.unshift(newObj);
								this.options4 = res.data.payload;
							}

						}
					})
				} else {
					this.options4 = [];
				}
			},

		}
	}
